.slide-top {
  background-position: left;
}

.slide-bottom {
  background-position: right;
}

.slide-top,
.slide-bottom {
  background-size: 83% auto;
}
@media screen and (max-width: config("screens.md")) {
  .slide-top,
.slide-bottom {
    background-size: 100%;
    background-position: center;
  }
}

.slide-mobile {
  width: 100%;
  height: 100%;
}
.slide-mobile:nth-child(even) .quote {
  background-color: rgba(56, 75, 0, 0.8);
}
.slide-mobile:nth-child(odd) .quote {
  background-color: rgba(206, 110, 25, 0.8);
}

.quote {
  width: 100%;
  max-width: 20%;
  height: 100%;
  min-height: 420px;
}
@media screen and (max-width: config("screens.md")) {
  .quote {
    max-width: 100%;
    max-height: 300px;
  }
}

.testimonial-carousel {
  margin-bottom: -1px;
