}
.text-content .text p:first-child {
  margin-top: 0;
}
.text-content .text p:last-child {
  margin-bottom: 0;
}
.text-content .image img {
  width: 363px !important;
  max-width: 100vw !important;
}

.bg-img {
  transition: all 200ms ease-in-out;
  background-repeat: no-repeat;
  background-position: cover;
  background-color: config("colors.orange");
  background-blend-mode: multiply;
  opacity: 0;
}

.menu-link {
  transition: all 200ms ease-in-out;
  background-color: transparent;
}
.menu-link > h4,
.menu-link > h2 {
  transition: all 200ms ease-in-out;
}
.menu-link:hover {
  background-color: config("colors.orange");
}
.menu-link:hover > h4,
.menu-link:hover > h2 {
  color: white;
}
.menu-link:hover > .bg-img {
  opacity: 0.5;
}

.single-menu > .absolute {
  height: 100px;
  top: 135px;
  left: 15px;
}

.descriptor > h2 {
  height: 33px;
  width: 33px;
  line-height: 33px;
}

.inline-descriptor .descriptor > h2 {
  height: 23px;
  width: 23px;
  line-height: 23px;
  font-size: 13px !important;
}

.meal > h2 {
  margin-top: 70px;
}

.meal:first-child > h2 {
  margin-top: 0;
}

.boxed-lunch-item:last-of-type > .desc {
  border-bottom: none;
}

.sub-heading {
  width: 240px;
  height: 240px;
  display: flex;
  justify-content: center;
  align-items: center;
}

a[id*=CONTROL].menu-link-item div h4,
a[id*=CONTROL].menu-link-item div h3 {
  background-color: transparent;
}
a[id*=CONTROL].menu-link-item div:hover h4,
a[id*=CONTROL].menu-link-item div:hover h2 {
  background-color: white;
  box-shadow: none;
  color: config("colors.orange");
}
a[id*=CONTROL].menu-link-item > div {
  height: 300px;
  background-color: transparent;
  background-blend-mode: multiply;
  transition: all 200ms ease-in-out;
  box-shadow: none;
}
a[id*=CONTROL].menu-link-item > div h3, a[id*=CONTROL].menu-link-item > div h4 {
  transition: all 200ms ease-in-out;
}
a[id*=CONTROL].menu-link-item > div:after {
  transition: all 200ms ease-in-out;
}
a[id*=CONTROL].menu-link-item > div:hover {
  box-shadow: none;
}
a[id*=CONTROL].menu-link-item > div:hover:after {
  transition: all 200ms ease-in-out;
  position: absolute;
  content: "";
  bottom: -200px;
  left: 0;
  width: 100%;
  height: 200px;
  background-color: config("colors.orange");
}

